
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';

import {ReactComponent as VinylLoader} from '../../assets/vinylColoredOrange.svg'
import './Loader.scss'


function Loader(props) {

  return (
    <>
        <Container className='align-items-center justify-content-center bg-white' fluid>
            <Row noGutters className="justify-content-center align-items-center mt-5">
                <span className='vinyl-loader-container'>
                <VinylLoader width='200' height='200' className="vinyl-loader"/>
                </span>
            </Row>
            <Row noGutters className="justify-content-center align-items-center text-center">
                <span className='text-center'><p className='vinyl-loader-text'> Loading ... </p></span>
            </Row>
        </Container>
    </>
  );
}

export default Loader;