
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import musicAny from '../../assets/mainBanner.png'
import musicAny2 from '../../assets/theme2.png'
import CardMusic from '../../components/CardMusic/index.jsx';


function ListOfSound() {
  return (
    <>
    <Container fluid>
<Row noGutters>
      <CardMusic
              id={1}
              titleCardMusic={"Le cours de la méthode"}
              imageCardMusic={musicAny}
              descriptionCardMusic={"Méthodologie pour l'exercice de la composition et de l’étude critique de document"}
              />
        <CardMusic
        id={2}
        titleCardMusic={"La dimension politique de la guerre"}
        imageCardMusic={musicAny2}
        descriptionCardMusic={`Premier morceau consacré au programme de Terminale de HGGSP, "La dimension politique de la guerre" s'intéresse à la façon avec laquelle la guerre a été conçue en Occident et comment elle a évolué depuis le XVIIIe siècle jusqu'à nos jours. Le prochain morceau consacré au thème 2 du programme reprendra l'AXE 2 en s'intéressant à "La construction de la paix".`}
        />
</Row>


    </Container>

    </>
  );
}

export default ListOfSound;