import 'bootstrap/dist/css/bootstrap.css';
import React, {useState,useEffect,useRef} from 'react'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Badge from 'react-bootstrap/Badge'
import Container from 'react-bootstrap/Container'
import translate from './i18n';
import Header from './components/Header/index.jsx'
import HomePage from './pages/HomePage/index.jsx'
import GamePage from './pages/GamePage/index.jsx'
import ListOfSound from './pages/ListOfSound/index.jsx'
import { HashLink } from 'react-router-hash-link';
import { Link } from 'react-router-dom';
import {
  Routes,
  Route
} from "react-router-dom";
import "./App.scss"
import Loader from './components/Loader';


function App() {

  const [appearNav,setAppearNav] =useState(false);
  const [loader,setLoader] = useState(true);
  useEffect(()=>{
setTimeout(()=> {setLoader(false)},3000)
  },[])

  return loader?
  (<Loader/>)
  :(
    <Container fluid>
      <Row noGutters>
      <Header appearNav={appearNav} setAppearNav={setAppearNav} />
      </Row>
<Row className={appearNav?'bg-dark vertical-header':'bg-dark vertical-header vertical-header--disable'}>
<HashLink className="btn btn-primary" to="/list-of-sound">{translate.t('rapAndPlay')}</HashLink>
          <HashLink className="btn btn-secondary" to="/professorap/#project">{translate.t('project')}</HashLink>
          <HashLink className="btn btn-secondary" to="/professorap/#contact">{translate.t('contact')}</HashLink>
          <a className="btn btn-success" href="https://linktr.ee/ma2sa" target='_blank' rel="noreferrer">{translate.t('findOtherMassaProduct')}</a>
        </Row>
      
   
    <Row noGutters>

    <Routes>
<Route path="/" element={<HomePage/>}></Route>
<Route path="/professorap" element={<HomePage/>}></Route>
<Route path="/list-of-sound" element={<ListOfSound/>}></Route>
<Route path="/game/:id" element={<GamePage/>}></Route>
</Routes>  
        </Row>

</Container>
  );
}

export default App;
