import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Container from 'react-bootstrap/esm/Container';
import { FacebookShareButton, TwitterShareButton,LinkedinShareButton,WhatsappShareButton,TelegramShareButton } from "react-share";
import { FacebookIcon, TwitterIcon,LinkedinIcon,WhatsappIcon,TelegramIcon  } from "react-share";
import './ModalStepContact.scss'


const sendToNextModal = (number,setFunctionNextModal)=>{
    setFunctionNextModal(number);
}

export  function StepContactMeOne(props) {
    
    return(
        <Container fluid>
              <Row noGutters>
  <p>C'était génial de t'avoir sur notre plateforme! Si tu aimes notre travail, partage le sur tes réseaux sociaux en cliquant sur le pouce:</p> 
    </Row>
{/*    <Row noGutters>
      <h3> Avis:</h3>
    </Row>*/}
    <Row className='my-4 p-0 justify-content-center'>
      {/*<Col><span className=" bg-secondary p-2 rounded-circle h1" onClick={()=>{sendToNextModal(2,props.setStepNumber)}}>👎</span></Col>*/}
      <span className=" m-0 p-0 bg-secondary rounded-circle h1 emoji-styled" onClick={()=>{sendToNextModal(1,props.setStepNumber)}}><p>👍</p></span>
     {/* <Col><span className=" bg-secondary p-2 rounded-circle h1" onClick={()=>{sendToNextModal(1,props.setStepNumber)}}> 🤩</span></Col>*/}
      </Row>
        </Container>

    )
}

export function StepContactMeTwo(props) {
    
    return(
        <Container fluid>
            <Row noGutters>
                <h4> Génial! Tu as aimé notre concept, pourrais-tu partager sur tes réseaux:</h4>
            </Row>
              <Row noGutters>
                <Col>
                <FacebookShareButton
        url={"https://kribean.github.io/professorap/"}
        quote={"La plateforme qui ralie rap et cours"}
        hashtag={"#"}
        description={"J'ai essayé le cours de la méthode, c'était génial! Rendez-vous sur la plateforme de Massa, n'hésitez pas à ajouter mon Facebook: https://www.facebook.com/ma2sa.professorap "}
        className="Demo__some-network__share-button"
      >
        <FacebookIcon size={32} round /> 
        <p>Facebook</p>
      </FacebookShareButton>
                </Col>

      <Col>
      <TwitterShareButton
        title={"Professorap"}
        url={"https://kribean.github.io/professorap/"}
        hashtags={["professorap", "hiphop","histoiregeo"]}
      >
        <TwitterIcon size={32} round />
        <p>Twitter</p>
      </TwitterShareButton>
      </Col>

      <Col>
      <LinkedinShareButton
        title={"Professorap"}
        url={"https://kribean.github.io/professorap/"}
        hashtags={["professorap", "hiphop","histoiregeo"]}
      >
        <LinkedinIcon size={32} round />
        <p>LinkedIn</p>
      </LinkedinShareButton>
      </Col>

      <Col>
      <WhatsappShareButton
        title={"Professorap"}
        url={"https://kribean.github.io/professorap/"}
      >
        <WhatsappIcon size={32} round />
        <p>Whatsapp</p>
      </WhatsappShareButton>
      </Col>

      <Col>
      <TelegramShareButton
        title={"Professorap"}
        url={"https://kribean.github.io/professorap/"}
      >
        <TelegramIcon size={32} round />
        <p>Telegram</p>
      </TelegramShareButton>
      </Col>

      </Row>
        </Container>

    )
}

export  function StepContactMeThree(props) {

    return(
        <Container fluid>
              <Row noGutters>
  <h3>Nous avons bien noté ton avis</h3>
  <p>Peux-tu nous dire en quoi nous pourrions être plus efficace?</p> 
 <textarea name="avis" cols="30" rows="10"></textarea>
      </Row>
        </Container>

    )
}