
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './SectionHomePage.scss'


function SectionHomePage(props) {
  return (
    <>
    <Container fluid>
        <Row noGutters>
            <h2>
            {props.title}
            </h2>
        </Row>
        <Row noGutters>
            <Col xs={12} md={6} className={props.toRight?"order-1":"order-2"}>
                <img className="image-container-section-home-page" src={props.imageContent} alt="to be confirnmed" />

            </Col>
            <Col xs={12} md={6} className={props.toRight?"order-2":"order-1"}>
            <p className='text-content-who-we-are'>
                    {props.textContent}
                </p>
            </Col>
        </Row>

    </Container>

    </>
  );
}

export default SectionHomePage;