import React, {useState,useEffect,useRef} from 'react'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Badge from 'react-bootstrap/Badge'
import Container from 'react-bootstrap/Container'
import translate from '../../i18n.js';

import './ProgressBar.scss'



function ProgressBar(props) {


  return (
<Container fluid>
<Row noGutters className='m-4 '>
                <h2> Tu as complété le son à:{parseInt(props.rateRanking)?parseInt(props.rateRanking):0} % </h2>
                </Row>
<Row noGutters className='m-4 border align-items-center ' style={{height:'40px',borderRadius:'40px'}}>
                <span className='progress-bar-rate' style={{height:'100%',width:`${props.rateRanking}%`}}/>
                </Row>
</Container>

  );
}

export default ProgressBar;
