
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Navbar from 'react-bootstrap/Navbar';
import { HashLink } from 'react-router-hash-link';
import {ReactComponent as NoteMusic} from '../../assets/logoMassa1.svg'
import translate from '../../i18n.js';
import Dropdown from 'react-bootstrap/Dropdown';
import {useRef,useEffect,useState} from 'react'
import './Header.scss'

function Header(props) {
    const [language, setLanguage] = useState('fr');
    useEffect(()=>{},[])

    const changeLang = (value)=>{
   
        translate.changeLanguage(language)
    .then(()=>{
     
        translate.options.lng = value;
        setLanguage(value)})
    }
  return (
    <>
<Navbar bg="dark">
        <Container>
          <Navbar.Brand>
            
<HashLink className='justify-content-center' to="/professorap">
<NoteMusic className="logo-design" />
            <p className='h2 text-white'>{translate.t('title')}</p>
</HashLink>
          </Navbar.Brand>

          <select class="btn btn-sm btn-light" aria-label="Default select example" onChange={(e)=>{changeLang(e.target.value);}}>
  <option value="en">Français</option>
  <option value="fr">English</option>
</select>

<div className='m-0 p-0 show-menu-header'>
<HashLink className="btn btn-primary m-4" to="/list-of-sound">{translate.t('rapAndPlay')}</HashLink>
          <HashLink className="btn btn-secondary m-4" to="/professorap/#project">{translate.t('project')}</HashLink>
          <HashLink className="btn btn-secondary m-4" to="/professorap/#contact">{translate.t('contact')}</HashLink>
          <a className="btn btn-success m-4" href="https://linktr.ee/ma2sa"  target='_blank' rel="noreferrer">{translate.t('findOtherMassaProduct')}</a>
          
</div>
<div className='m-3 p-0 show-menu-burger show-menu-burger--able' onClick={()=>{props.setAppearNav(!props.appearNav)}}>
<span className={props.appearNav?'burger-stripe burger-stripe-ichi': 'burger-stripe '}/>
<span className={props.appearNav?'burger-stripe burger-stripe-ni':'burger-stripe'}/>
<span className={props.appearNav?'burger-stripe burger-stripe-san':'burger-stripe'}/>
</div>
        </Container>
      </Navbar>
    </>
  );
}

export default Header;