import { useRef, useState} from "react";
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Container from 'react-bootstrap/Container'
//import {ReactComponent as MicrophoneNotRecording} from '../../assets/microphoneNotRecording.svg'
import {ReactComponent as MicrophoneRecording} from '../../assets/microphoneRecording.svg'
import instruMassaCoursMethod from '../../assets/instruMassaCoursMethod.mp3'
import instruMassaCoursMethod2 from '../../assets/massaCourInstru2.wav'
import SpeechRecognition, {
  useSpeechRecognition
} from "react-speech-recognition";
import "./SectionPlayAndRecord.scss";
import ReactAudioPlayer from 'react-audio-player'
import translate from '../../i18n.js';
import ModalSubsribe from '../../components/ModalSubscribe/index.jsx'
import { useParams } from "react-router-dom";

export default function SectionPlayAndRecord(props) {

  const {id} = useParams();

  const [show, setShow] = useState(false);
  const [stepNumber, setStepNumber] =useState(0);

  const handleClose = () => {
    setShow(false);
    setStepNumber(0)};
  
  const commands = [
    {
      command: "open *",
      callback: (website) => {
        window.open("http://" + website.split(" ").join(""));
      }
    },
    {
      command: "change background colour to *",
      callback: (color) => {
        document.body.style.background = color;
      }
    },
    {
      command: "reset",
      callback: () => {
        handleReset();
      }
    },
    {
      command: "reset background colour",
      callback: () => {
        document.body.style.background = `rgba(0, 0, 0, 0.8)`;
      }
    }
  ];
  const [boolResetAppear,setBoolResetAppear]=useState(false);
  const { transcript, resetTranscript } = useSpeechRecognition({ commands });
  const [isListening, setIsListening] = useState(false);
  const microphoneRef = useRef(null);
  if (!SpeechRecognition.browserSupportsSpeechRecognition()) {
    return (
      <div className="mircophone-container">
        Browser is not Support Speech Recognition.
      </div>
    );
  }
  const handleListing = () => {
    setBoolResetAppear(false);
    setIsListening(true);
    microphoneRef.current.classList.add("listening");
    SpeechRecognition.startListening({
      continuous: true,
      language: 'fr-FR'
    });
  };
  const stopHandle = () => {
    setIsListening(false);
    microphoneRef.current.classList.remove("listening");
    SpeechRecognition.stopListening();
  };

  const compareMySongToMassa = ()=>{
  
    props.setRateRanking(
      transcript?.replaceAll(' ','').length*100/2757
    )
  }
  const handleReset = () => {
    compareMySongToMassa();
    stopHandle();
    resetTranscript();
    setTimeout(()=>{setShow(true)},2000)
  };
  return (
    <Container className="section-play-and-record" fluid>
<Row>
<ReactAudioPlayer
  src={(id==='1')?instruMassaCoursMethod:instruMassaCoursMethod2}
  autoPlay={true}
  controls={true}
  onPlay={handleListing}
  onEnded={()=>{stopHandle();setBoolResetAppear(true);}}
/>
</Row>


      <Row noGutters className="m-1 align-items-center justify-content-center">
        <Col
          ref={microphoneRef}
          onClick={handleListing}
        >
                {(isListening || !transcript)&&
                <MicrophoneRecording className="m-1 p-0 microphone-icone-recording" width="72px" height="72px"/>
                }     
        </Col>
        <Col >
          {isListening ? `${translate.t('isRecording')}` : `${translate.t('clickToStart')}`}
        </Col>
        {isListening && (
          <button className="btn btn-success" onClick={()=>{stopHandle();setBoolResetAppear(true);}}>
            {translate.t('stop')}
          </button>
        )}
      </Row>
      {transcript && (
        <Row noGutters className="m-4">
{boolResetAppear&&(   <> 
          <button className="btn btn-danger" onClick={()=>{handleReset();setBoolResetAppear(false);}}>
          {translate.t('calculatePoint')}
          </button></>)}
        </Row>
      )}
      <ModalSubsribe 
      show={show} 
      handleClose={handleClose}
      stepNumber={stepNumber}
      setStepNumber={setStepNumber} />
    </Container>
  );
}

