
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import translate from '../../i18n.js';
import massaBandeau from '../../assets/massaBandeau.jpg'
import massa from '../../assets/massa.jpg'
import professoRapfrom from '../../assets/professoRap.png'
import './HomePage.scss'
import SectionHomePage from '../../components/SectionHomePage'
import SectionContact from '../../components/SectionContact'


function HomePage() {

  return (
    <>
    <Container fluid>
        <Row noGutters className="container-home-page justify-content-center align-items-center">
<Col md={6} >
<p className='h2 text-font-google'>
{translate.t('homepageContent').split('!')[0]+' !'}
</p>
<p className='h2 text-font-google'>
{translate.t('homepageContent').split('!')[1]}
</p>

</Col >
<Col className='image-container'>
<Row noGutters className='justify-content-center'>
<div className='massa-bandeau'>
 <img   src={massaBandeau} alt="Massa" />
 </div>
</Row>

</Col>
        </Row>
        <div className='m-0 p-0' id="project">
                <SectionHomePage
                title={translate.t('whoWeAre')}
        imageContent={professoRapfrom}
        textContent={translate.t('projectContent')}
        toRight={true}
        />
        </div>
        
        <div className='m-0 p-0' id="contact">
        <SectionContact/>
        </div>

    </Container>

    </>
  );
}

export default HomePage;