import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import {StepContactMeOne,StepContactMeTwo,StepContactMeThree} from '../../components/ModalStepContact'
import { useState } from 'react';
import { useEffect } from 'react';

export default function ModalSubsribe(props) {
    
    
    return(
<Modal show={props.show} onHide={props.handleClose}>
<Modal.Header closeButton>
  <Modal.Title>Merci de ta visite!!</Modal.Title>
</Modal.Header>
<Modal.Body>
{(props.stepNumber===0)&&<StepContactMeOne setStepNumber={props.setStepNumber}/>}
{(props.stepNumber===1)&&<StepContactMeTwo setStepNumber={props.setStepNumber}/>}
{(props.stepNumber===2)&&<StepContactMeThree/>}
      </Modal.Body>
<Modal.Footer>
  <Button variant="secondary" onClick={props.handleClose}>
    Fermer
  </Button>
</Modal.Footer>
</Modal>)
}