
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import translate from '../../i18n.js';
import './CardMusic.scss'
import { useNavigate } from "react-router-dom";

function CardMusic(props) {
    const navigate = useNavigate();
    const goToGame= ()=>{
        navigate(`/game/${props?.id}`)
    }
  return (
    <>
    <Container className='card-music m-3' onClick={()=>{goToGame()}} fluid>
<Row noGutters className='card-music-image align-items-center'>
 <img src={props.imageCardMusic} alt="music" />
</Row>
<Row noGutters className='card-music-title'>
<h3>
    {props.titleCardMusic}
</h3>
</Row>
<Row noGutters className='card-music-description'>
<p>{props.descriptionCardMusic}</p>
</Row>


    </Container>

    </>
  );
}

export default CardMusic;