import { useEffect, useRef, useState } from "react";
import Row from 'react-bootstrap/Row'
import Container from 'react-bootstrap/Container'
//import {ReactComponent as MicrophoneNotRecording} from '../../assets/microphoneNotRecording.svg'
import MassaCoursMethod from '../../assets/massaCoursMethod.mp3'
import MassaCoursMethod2 from '../../assets/massaCour2.wav'
import ReactAudioPlayer from 'react-audio-player'
import translate from '../../i18n.js';
import { useParams } from "react-router-dom";
export default function SectionMassaPlay() {
//const [loading,setLoading]=useState(false)

const {id}=useParams();


  return (
    <Container className="section-play-and-record" fluid>
<Row>
<ReactAudioPlayer
onLoadedMetadata={()=>{console.log('meta data loaded')}}
  src={(id==='1')?MassaCoursMethod:MassaCoursMethod2}
  autoPlay={true}
  controls={true}
 onPlay={()=>{console.log('is on')}}
  
/>
</Row>

    </Container>
  );
}

