import React, {useState} from 'react'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Container from 'react-bootstrap/Container'
import translate from '../../i18n.js';
import BannerGame  from "../../components/BannerGame"
import BannerSocialMedia  from "../../components/BannerSocialMedia"
import SectionPlayAndRecord  from "../../components/SectionPlayAndRecord"
import SectionTextRap  from "../../components/SectionTextRap"
import SectionMassaPlay from '../../components/SectionMassaPlay'
import ProgressBar from '../../components/ProgressBar'
import './GamePage.scss'


function GamePage() {
    const [listenMassa,setListenMassa]=useState(true)
    const [rateRanking,setRateRanking]=useState(0);

  return (
    <Container className='position-relative' fluid>
        <BannerGame/>
        <BannerSocialMedia/>
        {!listenMassa&&<ProgressBar rateRanking={rateRanking} setRateRanking={setRateRanking} />}
        <Row noGutters className='w-50 section-media-player-btn'>
            <button onClick={()=>{setListenMassa(!listenMassa)}} className={listenMassa? " m-4 btn btn-success btn-font" : " m-4 btn btn-warning  btn-font"}>
                {listenMassa?`${translate.t('singYouCan')}`:`${translate.t('massaListen')}`}
                </button>
        </Row>
        <Row noGutters>
<Col className='section-media-player'>
{listenMassa?
            <SectionMassaPlay/>
            :
            <SectionPlayAndRecord setRateRanking={setRateRanking}/>
            
            }
</Col>
            <Col className='order-1'>
            <SectionTextRap/>
            </Col>
        </Row>
</Container>
  );
}

export default GamePage;
